"use client";

import React, { useState, useEffect, useRef } from "react";
import { useParams, useRouter } from "next/navigation";
import styles from "./AddPost.module.css";
import "quill/dist/quill.snow.css";

const EditPost = () => {
  const { year, month, day, slug } = useParams(); // get post identifier
  const router = useRouter();
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [image, setImage] = useState(null);
  const [tags, setTags] = useState([]);
  const [status, setStatus] = useState("DF");
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const quillRef = useRef(null);
  const editorRef = useRef(null);

  const token =
    typeof window !== "undefined" ? localStorage.getItem("accessToken") : null;

  // Fetch the post to edit
  useEffect(() => {
    const fetchPost = async () => {
      try {
        const res = await fetch(
          `http://127.0.0.1:8000/api/blog/posts/${year}/${month}/${day}/${slug}/`
        );
        if (!res.ok) throw new Error(`Failed to fetch post: ${res.status}`);
        const data = await res.json();
        const post = data.post;
        setTitle(post.title);
        setDescription(post.body);
        setTags(post.tags || []);
        setStatus(post.status);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    fetchPost();
  }, [year, month, day, slug]);

  // Initialize Quill editor
  useEffect(() => {
    if (!editorRef.current || loading) return;

    const container = document.createElement("div");
    container.style.minHeight = "200px";
    container.style.background = "white";
    editorRef.current.innerHTML = "";
    editorRef.current.appendChild(container);

    let quillInstance;

    const initQuill = async () => {
      const Quill = (await import("quill")).default;

      quillInstance = new Quill(container, {
        theme: "snow",
        placeholder: "Write your description here...",
        modules: {
          toolbar: [
            [{ header: [1, 2, 3, false] }],
            ["bold", "italic", "underline", "strike"],
            [{ list: "ordered" }, { list: "bullet" }],
            ["link", "image"],
            ["clean"],
          ],
        },
      });

      quillInstance.root.innerHTML = description;

      quillInstance.on("text-change", () => {
        setDescription(quillInstance.root.innerHTML);
      });

      quillRef.current = quillInstance;
    };

    initQuill();

    return () => {
      if (quillRef.current) {
        quillRef.current.off("text-change");
        quillRef.current = null;
      }
      if (editorRef.current) editorRef.current.innerHTML = "";
    };
  }, [loading, description]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);
    setError("");
    setSuccess("");

    const formData = new FormData();
    formData.append("title", title);
    formData.append("body", description);
    formData.append("status", status);
    if (image) formData.append("image", image);
    if (tags.length > 0) tags.forEach((tag) => formData.append("tags", tag));

    try {
      const res = await fetch(
        `http://127.0.0.1:8000/api/blog/posts/${year}/${month}/${day}/${slug}/edit/`,
        {
          method: "PUT",
          headers: {
            Authorization: `Bearer ${token}`,
          },
          body: formData,
        }
      );

      if (!res.ok) {
        const data = await res.json();
        throw new Error(JSON.stringify(data));
      }

      setSuccess("Post updated successfully!");
      router.push(`/posts/${year}/${month}/${day}/${slug}`); // redirect to post view
    } catch (err) {
      setError("Failed to update post: " + err.message);
    } finally {
      setSaving(false);
    }
  };

  if (loading) return <p>Loading post...</p>;

  return (
    <div className={styles.addPostContainer}>
      <h1>Edit Post</h1>

      <form className={styles.form} onSubmit={handleSubmit}>
        {/* Title */}
        <input
          type="text"
          placeholder="Title"
          className={styles.input}
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          required
        />

        {/* Image */}
        <input
          type="file"
          accept="image/*"
          className={styles.input}
          onChange={(e) => setImage(e.target.files[0])}
        />

        {/* Quill Editor */}
        <label>Description</label>
        <div
          ref={editorRef}
          style={{
            minHeight: "200px",
            background: "white",
            marginBottom: "1rem",
          }}
        />

        {/* Tags */}
        <select
          className={styles.input}
          value={tags[0] || ""}
          onChange={(e) => setTags([e.target.value])}
        >
          <option value="" disabled>
            Select a tag
          </option>
          <option value="Creative Writing">Creative Writing</option>
          <option value="Poems">Poems</option>
          <option value="Short Stories">Short Stories</option>
          <option value="Literary Analysis">Literary Analysis</option>
          <option value="Entertainment">Entertainment</option>
        </select>

        {/* Draft/Published */}
        <select
          className={styles.input}
          value={status}
          onChange={(e) => setStatus(e.target.value)}
        >
          <option value="DF">Draft</option>
          <option value="PB">Published</option>
        </select>

        <button type="submit" className={styles.submitBtn} disabled={saving}>
          {saving ? "Saving..." : "Update Post"}
        </button>

        {error && <p style={{ color: "red" }}>{error}</p>}
        {success && <p style={{ color: "green" }}>{success}</p>}
      </form>
    </div>
  );
};

export default EditPost;
